gsap.registerPlugin(ScrollTrigger);
if(document.querySelector(".index-page")){
    indexPageAnim()
}
if(document.querySelector(".showroom-page")){
    showRoomPageAnim()
}

function indexPageAnim() {
    messagesAnim()
}


function messagesAnim() {
    let messages = document.querySelectorAll(".messages")

    messages.forEach(message => {
        console.log(message.closest(".messages"))
        let messageItems = message.querySelectorAll(".message")

        gsap.to(message, {
            yPercent: -40,
            xPercent: 10,
            duration: 2,
            ease: "power4",
            scrollTrigger: {
                trigger: message,
                start: 'bottom bottom',
                scrub: 1
            }
        })
    })
}


function showRoomPageAnim() {
    gsap.to('.functions-item._yellow', {
        backgroundImage: 'linear-gradient(225deg, #FFEEC1 0%, #FFF5EF 50%, #FADBCC 100%)',
        color: "#3F3F3F",
        scrollTrigger: {
            trigger: '.functions-item._yellow',
            scrub: true,
            start: 'top 80%',
            end: 'bottom 90%',
        }
    })
    gsap.to('.functions-item._green', {
        backgroundImage: 'linear-gradient(225deg, #A5E5C7 0%, #9CF3C9 42.19%, #4EB784 98.44%)',
        color: "#3F3F3F",
        scrollTrigger: {
            trigger: '.functions-item._green',
            scrub: true,
            start: 'top 80%',
            end: 'bottom 90%',
        }
    })
    gsap.to('.functions-item._blue', {
        backgroundImage: 'linear-gradient(225deg, #8DC6F6 0%, #B1D8FF 50.38%, #44B9EB 100%)',
        color: "#3F3F3F",
        scrollTrigger: {
            trigger: '.functions-item._blue',
            scrub: true,
            start: 'top 80%',
            end: 'bottom 90%',
        }
    })


    const tlTrifle = gsap.timeline({
        scrollTrigger: {
            trigger: '.trifle',
            start: 'top 60%',
            end: 'bottom 70%',
            scrub: true,
            toggleActions: 'none none none none'
        }
    })
    tlTrifle.to('.trifle__icon1 img', {
        scale: 1
    })
    .to('.trifle__icon2 img', {
        scale: 1
    },"<")
    .to('.message:nth-child(1)', {
        opacity: 1,
        duration: 2
    },"+=1")
    .to('.message:nth-child(2)', {
        opacity: 1,
        duration: 2
    },"+=1")
}
