let calcButtons = document.querySelectorAll(".calc-btn")

calcButtons.forEach(btn => {
    btn.onmouseenter = function () {
        btn.classList.add("_hover-state")
        setTimeout(()=>{
            btn.classList.add("_hover-state2")
        },300)
    }
    btn.onmouseleave = function () {
        btn.classList.remove("_hover-state")
        btn.classList.remove("_hover-state2")
    }
})

let tabsParents = document.querySelectorAll("._tabs-parent")

tabsParents.forEach(tabsParent => {
    let tabs = tabsParent.querySelectorAll("._tab")
    let tabsContents = tabsParent.querySelectorAll(".tab-content")

    tabs.forEach((tab,index) => {
        tab.onclick = function () {
            let tabId = this.getAttribute("data-tab")
            if(index === 1){
                tabsParent.classList.add("_second-active")
            } else{
                tabsParent.classList.remove("_second-active")
            }

            tabs.forEach(tab => {
              tab.classList.remove("_active")
            })
            tab.classList.add("_active")

            tabsContents.forEach(tabsContent => {
                tabsContent.classList.remove("_active")
            })
            tabsParent.querySelector(`.tab-content.${tabId}`).classList.add("_active")
        }
    })
})


