let homeItems = document.querySelectorAll(".home-item")

homeItems.forEach(homeItem => {
    let switcher = homeItem.querySelector(".m-switch input")

    if(switcher.checked){
        homeItem.classList.add("_active")
    }

    switcher.onchange = function () {
        if(switcher.checked){
            homeItem.classList.add("_active")
        } else{
            homeItem.classList.remove("_active")
        }
    }
})

let paralaxBlocks = document.querySelectorAll("._paralax-block")

paralaxBlocks.forEach(paralaxBlock => {
    let paralaxItems = paralaxBlock.querySelectorAll(".paralax-item")
    body.onmousemove = function (e) {
        let amountMovedX = (e.clientX * -0.1) / 8;
        let amountMovedY = (e.clientY * -0.1) / 8;
        for (let i = 0; i < paralaxItems.length; i++) {
            paralaxItems[i].style.transform = "translate(" + amountMovedX + "px," + amountMovedY + "px)"
        }
    }
})


if (document.querySelector('#video')) {
    let video = document.querySelector('#video');

    video.addEventListener('click', function () {
        video.play();
    }, false);
    video.click();
    video.play();
}



