let homeSlider = new Swiper('.home-slider .swiper-container', {
    slidesPerView: 1,
    spaceBetween: 0,
    init: false,
    pagination: {
        el: '.home-slider .swiper-pagination',
        type: 'bullets',
        clickable: true,
    },
    on: {
        init: function () {
            setHouseBlackout(this)
        },
        slideChange: function () {
            setHouseBlackout(this)
        },
    },
})


const resizeHandlerSlider = () => {
    if (window.innerWidth <= 576) {
        homeSlider.init()
    }
}

resizeHandlerSlider()

window.addEventListener('resize', resizeHandlerSlider);

function setHouseBlackout(swiper) {
    let left = swiper.slides[swiper.activeIndex].dataset.left
    let top = swiper.slides[swiper.activeIndex].dataset.top
    let rect = document.querySelector(".mob-rect")
    rect.style.left = `${left}%`
    rect.style.top = `${top}%`
}

function Sliders() {
    let sliders = document.querySelectorAll("._slider")
    let SlidersArray = []

    sliders.forEach((slider,index) => {
        slider.setAttribute("data-slider-id",index)
        SlidersArray[index] = new Swiper(`._slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            navigation: {
                nextEl: `._slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `._slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `._slider[data-slider-id="${index}"] .swiper-pagination`,
                type: 'bullets',
                clickable: true,
            },
        })
    })
}

function feedSlider() {
    var swiper = new Swiper('._feed1 .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        speed: 8000,
        loop: true,
        loopAdditionalSlides: 10,
        allowTouchMove: false,
        autoplay: {
            delay: 0,
        }
    })
}

function feedSlider2() {
    var swiper = new Swiper('._feed2 .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        speed: 8000,
        loop: true,
        loopAdditionalSlides: 10,
        allowTouchMove: false,
        autoplay: {
            delay: 0,
            reverseDirection: true,
        }
    })
}

Sliders()
feedSlider()
feedSlider2()
